
#addServers{position:absolute;width:100%;height:100%
}
#addServers .serverList{padding:0;width:90%;height:400px;margin:60px auto
}
#addServers .serverList .server{position:relative;float:left;cursor:pointer;color:#fff;background-color:#1C367E;text-align:center;line-height:50px;width:180px;height:50px;margin:30px;border-radius:5px
}
#addServers .serverList .server:hover{background-color:#2E55C9
}
#addServers .serverList .server:hover>.editPan{display:block
}
#addServers .serverList .server .editPan{position:absolute;display:none;line-height:20px;top:0px;right:-35px;width:40px
}
#addServers .serverList .server .editIcon{height:20px;padding-left:5px
}
#addServers .serverList .server .editIcon:hover{color:red
}
#addServers .serverList .server .delIcon{height:20px;margin-bottom:10px;padding-left:5px
}
#addServers .serverList .server .delIcon:hover{color:red
}
#addServers .tip-text{padding-top:7px;margin-bottom:0;text-align:left;color:red
}
#addServers .addIcon{-webkit-transition:all 0.5s;transition:all 0.5s;width:60px;height:60px;cursor:pointer;background-color:#2A4DB0;background-image:url("/static/image/addServer_icon.png");background-repeat:no-repeat;background-position:center;border-radius:60px;margin-bottom:10px
}
#addServers .addIcon:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1);background-color:#286090;border-color:#204d74
}
#addServers .btn-box{position:absolute;top:50%;left:50%;margin-left:-30px;color:#fff
}
#addServers .btn-next{position:absolute;left:50%;bottom:60px;background-color:#2A4DB0;padding:10px 40px;margin-left:-62px
}
#addServers .addServerBox{position:absolute;color:#fff;z-index:1000;width:400px;height:380px;background:rgba(21,65,122,0.6);box-shadow:0 0 12px 5px #15427b;border:1px solid #2175ff;top:50%;left:50%;margin-top:-190px;margin-left:-200px
}
#addServers .addServerBox .top{width:100%;height:40px
}
#addServers .addServerBox .top .title-name{cursor:pointer;border-bottom:1px solid #2175ff;float:left;text-align:center;width:50%;line-height:40px;height:40px
}
#addServers .addServerBox .top .title-name.active{background-color:#3B68FA
}
#addServers .addServerBox .top .title-name.title0{border-right:1px solid #2175ff
}
#addServers .addServerBox .content{margin:50px 0
}
#addServers .addServerBox .bottom{text-align:center
}
#addServers .addServerBox .bottom .btn-primary{padding-left:60px;padding-right:60px
}
#addServers .editServerBox{position:absolute;color:#fff;z-index:1000;width:400px;height:380px;background:rgba(21,65,122,0.6);box-shadow:0 0 12px 5px #15427b;border:1px solid #2175ff;top:50%;left:50%;margin-top:-190px;margin-left:-200px
}
#addServers .editServerBox .top{width:100%;height:40px
}
#addServers .editServerBox .top .title-name{cursor:pointer;border-bottom:1px solid #2175ff;float:left;text-align:center;width:100%;line-height:40px;height:40px
}
#addServers .editServerBox .content{margin:50px 0
}
#addServers .editServerBox .bottom{text-align:center
}
#addServers .editServerBox .bottom .btn-primary{padding-left:60px;padding-right:60px
}



