
#setNTP{position:absolute;width:100%;height:100%
}
#setNTP .sub-title{margin-top:40px;color:#bbb
}
#setNTP .list-title{font-size:15px;font-weight:bold;margin-left:126px
}
#setNTP .selectPan{width:730px;margin:100px 0 50px 110px
}
#setNTP .selectPan .selfTitle{margin-top:4px;color:#fff;font-size:15px
}
#setNTP .selectPan .error-text{margin-top:4px;color:red
}
#setNTP .btn-pan{position:absolute;bottom:60px;left:50%;margin-left:-126px
}
#setNTP .btn-pan .btn-back{background-color:#2A4DB0;padding:10px 40px
}
#setNTP .btn-pan .btn-next{background-color:#2A4DB0;padding:10px 40px
}
#setNTP .serverList{padding:0;width:90%;height:200px;margin:0 auto
}
#setNTP .serverList .server{position:relative;float:left;cursor:pointer;color:#fff;background-color:#1C367E;text-align:center;line-height:50px;width:180px;height:50px;margin:30px;border-radius:5px
}
#setNTP .serverList .server:hover{background-color:#2E55C9
}
#setNTP .serverList .server .time_icon{position:absolute;display:none;top:13px;left:8px;width:24px;height:24px;background-image:url(/static/image/time_icon.png);background-size:cover
}
#setNTP .serverList .server .selected{display:block
}
#setNTP .result-text{color:#fff;font-size:20px
}



