
#chooseComponents{position:absolute;width:100%;height:100%;color:#fff
}
#chooseComponents .btn-next{position:absolute;left:50%;bottom:60px;background-color:#2A4DB0;padding:10px 40px;margin-left:-62px
}
#chooseComponents .default-color{color:#fff
}
#chooseComponents .install-type-pan{margin:60px auto;width:200px;text-align:center
}
#chooseComponents .choose-main-pan{width:330px;margin:0 auto 40px
}
#chooseComponents .choose-main-pan .showmainPan-title{margin-top:5px
}
#chooseComponents .table-pan{padding:0 60px
}
#chooseComponents .table-pan .el-table .cell{text-align:center
}
#chooseComponents .table-pan .el-table .cell .el-radio__inner::after{-webkit-transition:-webkit-transform 0s;transition:-webkit-transform 0s;transition:transform 0s;transition:transform 0s, -webkit-transform 0s
}



